import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../views/Index.vue'
import store from '@/store'
const originalPush = VueRouter.prototype.push;

VueRouter.prototype.push = function push(location, onResolve, onReject) {
  if (onResolve || onReject) return originalPush.call(this, location, onResolve, onReject)
  return originalPush.call(this, location).catch(err => err)
}


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Index',
    component: Index
  },
  {
    path: '/category',
    name: 'Category',
    component: () => import('@/views/Category')
  },
  {
    path: '/cart',
    name: 'Cart',
    meta: {
      login: true
    },
    component: () => import('@/views/Cart')
  },
  {
    path: '/user',
    name: 'My',
    meta: {
      login: true
    },
    component: () => import('@/views/My')
  },
  {
    path: '/search',
    name: 'Search',
    component: () => import('@/views/Search')
  },
  {
    path: '/order/:type',
    name: 'Order',
    props: true,
    meta: {
      login: true
    },
    component: () => import('@/views/Order')
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('@/views/Login')
  },
  {
    path: '/goodlist/:id',
    name: 'Goodlist',
    component: () => import('@/views/Goodlist')
  },
  {
    path: '/promotion/:id',
    name: 'Promotion',
    component: () => import('@/views/Promotion')
  },
  {
    path: '/detail/:id',
    name: 'Detail',
    meta: {
      hideTabBar: true
    },
    props: true,
    component: () => import('@/views/Detail')
  },
  {
    path: '/payment',
    name: 'Payment',
    props: true,
    component: () => import('@/views/Payment')
  },
  {
    path: '/address',
    name: 'Address',
    props: true,
    component: () => import('@/views/Address')
  },
  {
    path: '/user/add_manage',
    name: 'Addresslist',
    props: true,
    meta: {
      login: true
    },
    component: () => import('@/views/Addresslist')
  },
  {
    path: '/dataert',
    name: 'Dataert',
    component: () => import('@/views/Dataert')
  },
  {
    path: '/firstround/:userId',
    name: 'FirstRound',
    component: () => import('@/views/FirstRound')
  },
  {
    path: '/changepassword',
    name: 'ChangePassword',
    component: () => import('@/views/ChangePassword')
  },
  {
    path: '/president/:id',
    name: 'President',
    props: true,
    component: () => import('@/views/President')
  },
]

const router = new VueRouter({
  routes
})
router.beforeEach((to, from, next) => {
  if (to.meta.login && store.getters.token == "null") {
    next('/login');
  }
  if (to.meta.login && store.getters.token.length < 1) {
    next('/login');
  }
  if(to.meta.login && localStorage.getItem('token') == null){
     next('/login')
  }
  next()
})
export default router
