import Vue from 'vue'
import Vuex from 'vuex'
import axios from '@/plugins/axios'
//import { Number } from 'core-js'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    showloading: false,
    loginInfo: null,
    token: null,
    timeent:null
  },
  getters:{
    token(state){
      let s = state.token;//判断vuex的state里面有没有token
      if(!s){//如果vuex里面没有token去sessionStorage没有
        s=sessionStorage.getItem('token')
        if(!s){
          s = document.cookie.substr(6);//截取cookie数据取出token
          if(s.lastIndexOf('{"status"') != -1){
            let index=s.lastIndexOf('{"status"');
            s = s.substring(0,index-13);
          }
          state.token = s
          // let index=s.lastIndexOf('{"status"');
          // let i = s.substring(index+12,s.length);
          // let b = s.substring(0,index);
          // s={
          //   token: b,
          //   logininfo:i
          // }
          // console.log(index)
          // window.onbeforeunload = function() {
          //   let newTimes = new Date().getTime();
          //   console.log(newTimes)
          //  }();
          //设置过期时间
          // if(s.lastIndexOf('{"status"') != -1){
          //   let index=s.lastIndexOf('{"status"');
          //   //过期时间判断
          //   let oldTime = s.substring(index-13,index);
          //   console.log(oldTime)
          //   oldTime = Number(oldTime); 
          //   console.log(new Date(oldTime))
          //   let newTime = new Date().getTime();
          //   console.log(oldTime,newTime)
          //   if(oldTime<newTime){
          //     sessionStorage.removeItem('token');
          //     sessionStorage.removeItem('logininfo');
          //     document.cookie = "token=; expires=Thu, 01 Jan 1970 00:00:00 UTC;path=/;"
          //     s = "null";
          //   }
          // }
        }
        s =String( localStorage.getItem('token'))
      }
      return s;
    },
    loginUser(state){
      let u = state.loginInfo;
      if(!u){
        let a = sessionStorage.getItem('logininfo');
        u = JSON.parse(a);
      }
      if(!u){
        u = localStorage.getItem('logininfo')
        // console.log(u)
        u = JSON.parse(u)
      }
      return u;
    }
  },
  mutations: {
  
    //改变是否正在加载
    changeLoading(state, payload) {
      state.showloading = payload;
    },
    //存入token
    saveToken(state, payload) {
      state.token = payload
      document.cookie = "token=" + payload;
      sessionStorage.setItem('token', payload);
      localStorage.setItem('token', payload)
    },
    //保存登录信息
    saveLoginInfo(state,payload){
      state.loginInfo = payload;
      let loginfo = JSON.stringify(payload);
      document.cookie +=  loginfo;
      sessionStorage.setItem('logininfo',loginfo)
      localStorage.setItem('logininfo', loginfo)
    },
    //标记
    //保存登录时间
    saveTimeent(state, payload){
      state.timeent = payload
      document.cookie += payload;
    },
    //标记
    //退出登录
    loginOut(state){
      document.cookie = "token=; expires=Thu, 01 Jan 1970 00:00:00 UTC;path=/;"
      sessionStorage.removeItem('token');
      sessionStorage.removeItem('logininfo')
      localStorage.removeItem('token');
      localStorage.removeItem('logininfo')
      state.token = null;
      state.loginInfo = null;
    }
  },
  actions: {
    getLoginInfo(context) {
      //ajax获取登录数据 并保存在vuex
      axios.get('api/user', {
        //token 可以转移到axios的请求前置处理
        // headers:{
        //   'Authori-zation':'Bearer ' + payload
        // },
        // params:{//get传值
        //   token:payload
        // },
        // data:{//post传值
        //   token:payload
        // }
      }).then(d => {
        //登录数据获取成功存储vuex或sessionStorage
        context.commit('saveLoginInfo',d.data)
      })
    }
  },
  modules: {
  }
})
