<template>
  <div class="index">
    <div class="search">
      <van-image :src="logoUrl" />
      <van-search
        shape="round"
        @focus="jumpSearch"
        placeholder="请输入搜索关键词"
      />
    </div>
    <van-swipe :autoplay="3000" class="banner" @change="changeBanner">
      <van-swipe-item v-for="image in banner" :key="image.id">
        <img v-lazy="image.pic" />
      </van-swipe-item>
      <template v-slot:indicator>
        <div class="bannerradio">
          <span
            :class="{ active: bannerIndex == i - 1 }"
            v-for="i in banner.length"
            :key="i"
          ></span>
        </div>
      </template>
    </van-swipe>
    <van-grid icon-size="45px">
      <van-grid-item
        :icon="item.pic"
        :text="item.name"
        v-for="item in menu"
        @click="hoprouting(item.wap_url)"
        :key="item.id"
      />
    </van-grid>
    <van-notice-bar class="imgert" :scrollable="false" mode="link">
      <template #left-icon>
        <div class="imgertson">
          <img src="../assets/pay.png" alt="" />
        </div>
      </template>
      <van-swipe
        vertical
        class="notice-swipe imgertson2"
        :autoplay="3000"
        :show-indicators="false"
      >
        <van-swipe-item v-for="rolling in roll" :key="rolling.id">{{
          rolling.info
        }}</van-swipe-item>
      </van-swipe>
    </van-notice-bar>
    <div class="wrapper">
      <div class="wrapperson1">
        <van-cell-group>
          <van-cell center is-link title="快速选择" :label="fastInfo">
            <template #default>
              <router-link to="/category" class="more">
                <span class="more">更多</span>
              </router-link>
            </template>
            <template #icon>
              <router-link to="/category" class="more">
                <span class="more"></span>
              </router-link>
            </template>
          </van-cell>
        </van-cell-group>
      </div>
      <div class="djtoplists">
        <div class="container">
          <div class="djtoplist">
            <router-link :to="`/president/${fast.id}?title=${fast.cate_name}`"
             v-for="fast in fastList" :key="fast.id" tag="div">
              <img :src="fast.pic" alt="" />
              <p>{{ fast.cate_name }}</p>
            </router-link>
          </div>
        </div>
      </div>
    </div>
    <div class="wrapper">
      <div class="wrapperson1">
        <van-cell-group>
          <van-cell center is-link title="精品推荐" :label="bastInfo">
            <template #default>
                <span class="more" @click="hotgoods">更多</span>
            </template>
          </van-cell>
        </van-cell-group>
      </div>
      <boutique :item="bastBanner"></boutique>
      <quality :item="bastList"></quality>
    </div>
    <div class="hotList">
      <div class="hot-bg">
        <van-cell-group class="titlert">
          <van-cell center is-link>
            <template #title>
              <div>
                <span class="custom-title titleson">热门榜单</span>
                <span class="custom-title titleson2"
                  >根据销量、搜索、好评等综合得出</span
                >
              </div>
            </template>
            <template #default>
              <div class="titleson3" @click="hotlist">
                  <span class="more">更多</span>
              </div>
            </template>
          </van-cell>
        </van-cell-group>
      </div>
      <div class="listrow">
        <router-link :to="`/detail/${likeIn.id}`" class="listrowson1" 
        v-for="likeIn in likeInfo" :key="likeIn.id" tag="div">
          <div>
            <img :src="likeIn.image" alt="" />
            <p>{{ likeIn.store_name }}</p>
            <p>￥{{ likeIn.price }}</p>
          </div>
        </router-link>
      </div>
    </div>
    <div class="wrapper">
      <van-cell-group>
        <van-cell center is-link title="新品首发" :label="firstInfo">
          <template #default>
              <span class="more" @click="firstRound">更多</span>
          </template>
        </van-cell>
      </van-cell-group>
      <div class="djtoplists">
        <div class="containers">
          <div class="djtoplistes">
            <router-link :to="`/detail/${firList.id}`" v-for="firList in firstList" :key="firList.id" tag="div">
              <img :src="firList.image" alt="" />
              <p>{{ firList.store_name }}</p>
              <p>￥{{ firList.price }}</p>
            </router-link>
          </div>
        </div>
      </div>
    </div>
    <div class="wrapper">
      <van-cell-group>
        <van-cell center is-link title="促销单品" :label="salesInfo">
          <template #default>
            <span class="more" @click="promotion">更多</span>
          </template>
        </van-cell>
      </van-cell-group>
      <div class="goodList">
        <van-card v-for="bast in benefit" :key="bast.id">
        <template #thumb>
        <div class="thumbimg">
          <router-link :to="`/detail/${bast.id}`" tag="div">
            <img :src="bast.image" alt="" />
          </router-link>
        </div>
      </template>
          <template #title>
            <div class="tabsons">{{ bast.store_name }}</div>
          </template>
          <template #origin-price>
            <div class="saless">日常价:￥{{ bast.ot_price }}</div>
          </template>
          <template #price-top>
            <div class="vipprice2">促销价:￥{{ bast.price }}</div>
          </template>
          <template #num>
            <div class="sales">仅剩:{{ bast.stock }}{{ bast.unit_name }}</div>
          </template>
        </van-card>
      </div>
    </div>
    <!-- <button @click="changeLoading(true)">显示加载</button>
    <button @click="changeLoading(false)">隐藏加载</button> -->
  </div>
</template>
<script>
import { mapMutations } from "vuex";
import Boutique from "@/components/Boutique";
import Quality from "@/components/Quality";
export default {
  data() {
    return {
      logoUrl: "",
      indexDate: null,
      banner: [],
      menu: [],
      bannerIndex: 0,
      roll: [],
      fastInfo: "",
      fastList: [],
      bastInfo: "",
      bastBanner: [],
      bastList: [],
      likeInfo: [],
      firstList: [],
      firstInfo: "",
      salesInfo: "",
      benefit: [],
    };
  },
  components: {
    Boutique,
    Quality,
  },
  created() {
    this.changeLoading(true);
    this.$axios.get("api/index").then((d) => {
      this.indexDate = d.data.data;
      this.logoUrl = this.indexDate.logoUrl;
      this.banner = this.indexDate.banner;
      this.menu = this.indexDate.menus;
      this.roll = this.indexDate.roll;
      this.fastInfo = this.indexDate.info.fastInfo;
      this.fastList = this.indexDate.info.fastList;
      this.bastInfo = this.indexDate.info.bastInfo;
      this.bastBanner = this.indexDate.info.bastBanner;
      this.bastList = this.indexDate.info.bastList;
      this.likeInfo = this.indexDate.likeInfo;
      this.firstList = this.indexDate.info.firstList;
      this.firstInfo = this.indexDate.info.firstInfo;
      this.salesInfo = this.indexDate.info.salesInfo;
      this.benefit = this.indexDate.benefit;
      this.changeLoading(false);
    });
  },
  methods: {
    hoprouting(event){
       if(event == "/user/add_manage"){
         this.$router.push({ path: "/user/add_manage"});
       }else if(event == "/order/list/"){
         this.$router.push({ path: "/order/1"});
       }else{
         this.$toast({
          message: "功能维护中",
          icon: "info-o"
        });
        return;
       }
    },
    promotion(){
       this.$router.push({ name: "Promotion", query: { userId: "4" ,titles:"促销单品"} });
    },
    hotgoods(){
       this.$router.push({ name: "FirstRound", query: { userId: "1" ,titles:"精品推荐"} });
    },
    hotlist(){
       this.$router.push({ name: "FirstRound", query: { userId: "2" ,titles:"热门榜单"} });
    },
    firstRound(){
       this.$router.push({ name: "FirstRound", query: { userId: "3",titles:"首发新品" } });
    },
    jumpSearch() {
      this.$router.push("/search");
    },
    changeBanner(index) {
      this.bannerIndex = index;
    },
    ...mapMutations(["changeLoading"]),
  },
};
</script>
<style lang="less">
.index {
  background-color: white;
  .van-grid{
    padding-top: 8px;
    .van-grid-item{
      .van-grid-item__content{
        padding: 5px 5px 8px 5px;
        .van-grid-item__text{
          margin-top: 5px;
          font-size: 13px;
        }
      }
    }
  }
  .banner {
    width: 100%;
    min-height: 187px;
    img {
      width: 100%;
      min-height: 187px;
      display: block;
    }
    .bannerradio {
      position: absolute;
      bottom: 10px;
      left: 50%;
      transform: translateX(-50%);
      display: flex;
      span {
        flex: 1;
        width: 10px;
        height: 2px;
        font-size: 12px;
        margin: 0 5px 0 5px;
        background-color: aliceblue;
        &.active {
          opacity: 0.5;
        }
      }
    }
  }
  .hotList {
    height: 220px;
    position: relative;
    .hot-bg {
      background: url(../assets/1.png) no-repeat;
      background-size: 375px 120.5px;
      width: 375px;
      height: 120px;
      .titlert {
        background-color: transparent;
        .van-cell-group {
          background-color: transparent;
        }
        .van-cell {
          background-color: transparent;
        }
        .van-cell__right-icon {
          color: #fff;
        }
        .van-cell__title {
          -webkit-flex: 7;
          .titleson {
            color: #fff;
            font-size: 15px;
            font-weight: 700;
            margin-right: 10px;
          }
          .titleson2 {
            color: #fff;
            font-size: 12px;
          }
        }
        .titleson3 {
          color: #fff;
          span {
            color: #fff;
          }
        }
      }
    }
    .listrow {
      width: 91.5%;
      margin: -74px 5% 0 5%;
      height: 175px;
      border-radius: 10px;
      background-color: white;
      padding: 0 11px;
      box-sizing: border-box;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .listrowson1 {
        width: 31.2%;
        height: 150px;
        border-radius: 5px;
        img {
          width: 100%;
          height: 101.25px;
          border-radius: 5px;
        }
        p {
          margin: 2px;
          font-size: 14px;
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
          overflow: hidden;
          &:nth-child(3) {
            color: #fc4141;
            font-weight: 700;
          }
        }
      }
    }
  }
  .wrapper {
    .more {
      color: #333;
    }
    .djtoplists {
      width: 100%;
      .container {
        overflow: hidden;
        width: 100%;
        height: 40vw;
        .djtoplist {
          height: 40vw;
          white-space: nowrap; /*文本不会换行，文本会在在同一行上继续*/
          overflow-y: auto; /*可猾动*/
          div {
            width: 25vw;
            margin: 6px;
            font-size: 14px;
            display: inline-block;
            img {
              width: 25vw;
              border-radius: 4vw;
              margin-bottom: 2.2vw;
            }
            &:nth-child(3n-2) {
              border-bottom: 2.6px solid #ff6960;
            }
            &:nth-child(3n-1) {
              border-bottom: 2.6px solid #47b479;
            }
            &:nth-child(3n) {
              border-bottom: 2.6px solid #579afe;
            }
            p {
              width: 25vw;
              line-height: 8vw;
              padding: 0 5px;
              box-sizing: border-box;
              border-left: 1px solid #eee;
              border-right: 1px solid #eee;
              margin: 0;
              text-align: center;
              display: -webkit-box;
              -webkit-line-clamp: 1;
              -webkit-box-orient: vertical;
              overflow: hidden;
            }
          }
        }
      }
      .containers {
        overflow: hidden;
        width: 100%;
        height: 52vw;
        padding: 0 10px;
        box-sizing: border-box;
        .djtoplistes {
          height: 52vw;
          white-space: nowrap; /*文本不会换行，文本会在在同一行上继续*/
          overflow-y: auto; /*可猾动*/
          div {
            width: 32vw;
            margin: 0px 5px;
            font-size: 14px;
            border: 1px solid #ddd;
            border-radius: 6px;
            display: inline-block;
            img {
              width: 32vw;
              height: 30vw;
              border-radius: 6px 6px 0 0;
              margin-bottom: 2.2vw;
            }
            p {
              width: 32vw;
              line-height: 19px;
              padding: 0 5px;
              box-sizing: border-box;
              margin: 0;
              text-align: center;
              display: -webkit-box;
              -webkit-line-clamp: 1;
              -webkit-box-orient: vertical;
              overflow: hidden;
              &:nth-child(2) {
                width: 25vw;
              }
              &:nth-child(3) {
                margin-bottom: 5px;
                color: #fc4141;
                font-weight: 700;
              }
            }
          }
        }
      }
    }
    .djtoplistes .wrapperson1 {
      .more {
        color: #000;
      }
    }
    // .wrapperson1 {
    //   height: 38px;
    //   padding-top: 7px;
    //   display: flex;
    //   align-items: center;
    //   justify-content: space-between;
    //   .text {
    //     width: 265px;
    //     height: 100%;
    //     div {
    //       &:first-child {
    //         height: 20px;
    //         font-size: 15px;
    //       }
    //       &:nth-child(2) {
    //         height: 16px;
    //         font-size: 12px;
    //         color: #999;
    //       }
    //     }
    //   }
    //   .more {
    //     font-size: 13px;
    //     color: #000;
    //   }
    // }
    .goodList {
       .thumbimg {
      width: 90px;
      display: flex;
      align-items: center;
      .buttonest {
        font-size: 10px;
      }
      img {
        width: 90px;
        height: 90px;
      }
    }
      .tabson {
        font-size: 15px;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }
      .saless {
        text-decoration: line-through;
      }
      .tabsons {
        font-size: 14px;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }
      .vipprice1 {
        font-size: 17px;
        color: #fc4141;
        font-weight: 700;
      }
      .vipprice2 {
        width: 100px;
        padding: 0 0.18rem;
        margin: 7px 0;
        background-color: red;
        height: 22px;
        line-height: 22px;
        background-image: -webkit-gradient(
          linear,
          left top,
          right top,
          from(#ff6248),
          to(#ff3e1e)
        );
        background-image: linear-gradient(90deg, #ff6248 0, #ff3e1e);
        font-size: 0.2rem;
        color: #fff;
        background-image: -moz-linear-gradient(
          to right,
          #ff6248 0,
          #ff3e1e 100%
        );
        border-radius: 0.24rem 0.03rem 0.24rem 0.03rem;
      }
      .van-card__origin-price {
        text-decoration: none;
      }
      .buttonindex {
        width: 26px;
        border: 1px solid #ff3700;
        height: 26px;
        border-radius: 50%;
        .buttonindexson {
          font-size: 16px;
          color: #ff3700;
        }
      }
    }
  }
  .van-image {
    width: 63.5px;
    height: 22.5px;
    margin-right: 12.5px;
  }
  .imgert {
    color: #000;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    background-color: #fff;
    display: flex;
    .imgertson {
      margin-right: 11.5px;
      width: 62px;
      img {
        height: 14px;
        width: 62px;
      }
    }
    .notice-swipe {
      height: 38.5px;
      line-height: 38.5px;
      width: 261.5px;
      color: #666;
    }
  }
  .search {
    display: flex;
    height: 49px;
    align-items: center;
    justify-content: center;
    .notice-swipe {
      height: 40px;
      line-height: 40px;
    }
    .van-search {
      width: 250px;
      padding: 0 14px;
    }
  }
}
</style>