import Vue from 'vue'
import './plugins/axios'
import App from './App.vue'
import router from './router'
import store from './store'
import './assets/animate4.1.min.css'
import './assets/font/iconfont.css'
import { Tabbar, TabbarItem,Search,Image as VanImage,Swipe, SwipeItem, 
  Lazyload,Grid, GridItem,Overlay,Loading,Tab,Tabs,Form,Field,Button,
Icon,Toast,NoticeBar,Card,Tag,Cell, CellGroup,Checkbox, CheckboxGroup,
GoodsAction, GoodsActionIcon, GoodsActionButton,Sku,SubmitBar,
ActionSheet,AddressEdit,Area,RadioGroup, Radio   } from 'vant';
//import 'vant/lib/index.css'

import './assets/resetcolor.less'
import 'amfe-flexible';

Vue.config.productionTip = false
Vue.use(Tabbar).use(TabbarItem)
Vue.use(Search).use(VanImage)
Vue.use(Swipe).use(SwipeItem);
Vue.use(Lazyload).use(Grid).use(GridItem)
Vue.use(Overlay).use(Loading)
Vue.use(Tab).use(Tabs)
Vue.use(Form).use(Field).use(Button).use(Icon).use(Toast)
Vue.use(NoticeBar).use(Card).use(Tag)
Vue.use(Cell).use(CellGroup)
Vue.use(GoodsAction).use(GoodsActionButton).use(GoodsActionIcon).use(Sku)
Vue.use(SubmitBar).use(Checkbox).use(CheckboxGroup)
Vue.use(ActionSheet).use(AddressEdit).use(Area)
Vue.use(RadioGroup).use(Radio)

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
