<template>
  <div id="app">
    <!-- <div id="nav">
      <router-link to="/">Home</router-link> |
      <router-link to="/about">About</router-link>
    </div> -->
    <van-tabbar
      active-color="#fc4141"
      :z-index="$route.meta.hideTabBar ? '-1' : 1"
      inactive-color="#282828"
      route
    >
      <van-tabbar-item icon="wap-home-o" to="/">首页</van-tabbar-item>
      <van-tabbar-item icon="apps-o" to="/category">分类</van-tabbar-item>
      <van-tabbar-item icon="shopping-cart-o" to="/cart"
        >购物车</van-tabbar-item
      >
      <van-tabbar-item icon="contact" to="/user">我的</van-tabbar-item>
    </van-tabbar>
    <div class="home" :style="{top:boxy+'px'}" @click="showest=!showest" @touchstart="touchstarts" @touchmove.prevent="touchmoves">
      <div class="row">
      <transition
        enter-active-class="animate__animated animate__fadeInRight animate__faster"
      >
      <div class="homeCon" v-show="showest" ref="homecon">
       <router-link to="/">
        <a><van-icon name="wap-home-o" /></a>
       </router-link>
       <router-link to="/cart">
        <a><van-icon name="shopping-cart-o" /></a>
        </router-link>
        <router-link to="/user">
        <a><van-icon name="contact" /></a>
        </router-link>
      </div>
      </transition>
      <div class="pictrueBox">
        <img src="./assets/close.jpg" alt="" />
      </div>
      </div>
    </div>
    <van-overlay :show="showloading">
      <div class="wraper-loader">
        <van-loading>加载中...</van-loading>
      </div>
    </van-overlay>
    <router-view />
    <div class="bottomban"></div>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      showest:false,
      boxy:380
    };
  },
  created() {

  },
  methods:{
    touchstarts(e){
      this.boxy = e.targetTouches[0].clientY - 30
      if(this.boxy<20){
        this.boxy = 20
      }else if(this.boxy+50>document.documentElement.clientHeight){
        this.boxy = document.documentElement.clientHeight - 50
      }
    },
    touchmoves(e){
      this.boxy = e.targetTouches[0].clientY - 30
      if(this.boxy<20){
        this.boxy = 20
      }else if(this.boxy+50>document.documentElement.clientHeight){
        this.boxy = document.documentElement.clientHeight - 50
      }
    },
  },
  computed: {
    ...mapState(["showloading"]),
  },
};
</script>
