<template>
  <div>
    <van-swipe :autoplay="3000" class="banner" @change="changeBanner">
      <van-swipe-item v-for="image in item" :key="image.id">
        <img v-lazy="image.img" />
      </van-swipe-item>
      <template v-slot:indicator>
        <div class="bannerradio">
          <span
            :class="{ active: bannerIndex == i - 1 }"
            v-for="i in item.length"
            :key="i"
          ></span>
        </div>
      </template>
    </van-swipe>
  </div>
</template>
<script>
export default {
    props:["item"],
    data(){
        return{
            bannerIndex: 0
        }
    },
    methods:{
        changeBanner(index) {
      this.bannerIndex = index;
    },
    }
}
</script>
<style lang="less" scoped>
     .banner {
    width: 100%;
    min-height: 187px;
    img {
      width: 100%;
      min-height: 187px;
      display: block;
    }
    .bannerradio {
      position: absolute;
      bottom: 10px;
      left: 50%;
      transform: translateX(-50%);
      display: flex;
      span {
        flex: 1;
        width: 10px;
        height: 2px;
        font-size: 12px;
        margin: 0 5px 0 5px;
        background-color: aliceblue;
        &.active {
          opacity: 0.5;
        }
      }
    }
  }
</style>